.App {
  text-align: center;
  margin-top: 50px;
}

body {
  /* background-color: rgba(99, 73, 73, 0.514); */
  /* background-color: #222; */
  background-image: url('https://images.pexels.com/photos/3745234/pexels-photo-3745234.jpeg?crop=entropy&cs=srgb&dl=pexels-alex-conchillos-3745234.jpg&fit=crop&fm=jpg&h=3456&w=4608');
  background-size: cover;
  background-size: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
